<template>
	<div class="center">
		<div class="title">
			<h1>Drop file to upload</h1>
		</div>

		<div class="dropzone">
			<img src="http://100dayscss.com/codepen/upload.svg" class="upload-icon" />
			<input type="file" @change="addfile" class="upload-input" />
		</div>

		<button type="button" class="btn" name="uploadbutton">Upload file</button>

	</div>
</template>

<script>
    import * as XLSX from 'xlsx'; //  Data Export Imports Related
    export default {
        name: 'UpdateFileComponents',
      data: function(){
    return {
      file: File,
      arrayBuffer: null,
      filelist: null
    };
  },
        methods: {
          addfile(event) {
  this.file = event.target.files[0];
  let fileReader = new FileReader();
  fileReader.readAsArrayBuffer(this.file);
  fileReader.onload = () => {
    this.arrayBuffer = fileReader.result;
    var data = new Uint8Array(this.arrayBuffer);
    var arr = new Array();
    for (var i = 0; i != data.length; ++i)
      arr[i] = String.fromCharCode(data[i]);
    var bstr = arr.join("");
    var workbook = XLSX.read(bstr, { type: "binary" });
    var first_sheet_name = workbook.SheetNames[3];
    var worksheet = workbook.Sheets[first_sheet_name];
    var datatable= XLSX.utils.sheet_to_json(worksheet, { raw: false })
    console.log(datatable[4]);

    this.filelist = [];
  };
}
        }
    }
</script>

<style>
    #app {
        text-align: center;
    }
</style>