import $ from 'jquery'

$(window).on('load pageshow', function () {
    $('body').removeClass("off");
    $('body').addClass("on"); 
});    
$("a:not([href*=javascript]):not([href*=\\#]):not(.fancybox):not([target]):not([data-fancybox])").click(function() {
    $('body').removeClass("on");
    $('body').addClass("off"); 
    let url = $(this).attr('href');
    window.setTimeout(function() {
        window.location.href = url;
    }, 400);    
    return false;
});


$(document).ready(function () {
    var itemsDiv = ('.MultiCarousel-inner');
    var itemWidth = "361";

    $('.leftLst, .rightLst').click(function () {
        var condition = $(this).hasClass("leftLst");
        if (condition)
            click(0, this);
        else
            click(1, this)
    });

    ResCarouselSize();




    $(window).resize(function () {
        ResCarouselSize();
    });

    //this function define the size of the items
    function ResCarouselSize() {

        var itemClass = ('.item');
        var id = 0;
        $(itemsDiv).each(function () {
            id = id + 1;
            var itemNumbers = $(this).find(itemClass).length;
    
            $(this).parent().attr("id", "MultiCarousel" + id);


          
            $(this).css({ 'transform': 'translateX(0px)', 'width': itemWidth * itemNumbers });
            $(this).find(itemClass).each(function () {
                $(this).outerWidth(itemWidth);
            });

            $(".leftLst").addClass("over");
            $(".rightLst").removeClass("over");

        });
    }


    //this function used to move the items
    function ResCarousel(e, el, s) {
        var leftBtn = ('.leftLst');
        var rightBtn = ('.rightLst');
        var translateXval = '';
        var divStyle = $(el + ' ' + itemsDiv).css('transform');
        var values = divStyle.match(/-?[\d]+/g);
        var xds = Math.abs(values[4]);
        if (e == 0) {
            translateXval = parseInt(xds) - parseInt(itemWidth * s);
            $(el + ' ' + rightBtn).removeClass("over");

            if (translateXval <= itemWidth / 2) {
                translateXval = 0;
                $(el + ' ' + leftBtn).addClass("over");
            }
        }
        else if (e == 1) {
            var itemsCondition = $(el).find(itemsDiv).width() - $(el).width();
            translateXval = parseInt(xds) + parseInt(itemWidth * s);
            $(el + ' ' + leftBtn).removeClass("over");

            if (translateXval >= itemsCondition - itemWidth / 2) {
                translateXval = itemsCondition;
                $(el + ' ' + rightBtn).addClass("over");
            }
        }

            $(el + ' ' + itemsDiv).css('transform', 'translateX(' + -translateXval + 'px)');
    }

    //It is used to get some elements from btn
    function click(ell, ee) {
        var Parent = "#" + $(ee).parent().attr("id");
        var slide = $(Parent).attr("data-slide");
        ResCarousel(ell, Parent, slide);
    }

});


function reveal(){
    var reveals = document.querySelectorAll(".h");
     var reveals_5 = document.querySelectorAll(".Link");
    var reveals_3 = document.querySelectorAll(".banner");
    var reveals_4 = document.querySelectorAll(".Navigation_Block");
    var reveals_2 = document.querySelectorAll(".t");
    var reveals_6 = document.querySelectorAll(".h2");
    var reveals_7 = document.querySelectorAll(".t2");
    var imglogo=document.querySelector('.LogoImg');
     for (var i = 0; i < reveals_2.length; i++) {
       var windowHeight = window.innerHeight;
       var elementTop = reveals_2[i].getBoundingClientRect().top;
       var elementVisible = 150;
       if (elementTop < windowHeight - elementVisible) {
         reveals[i].classList.add("show_Block");
       }
     }
   
      for (var k = 0; k < reveals_7.length; k++) {
       var elementTop_3 = reveals_7[k].getBoundingClientRect().top;
        var elementVisible_3 = 150;
       if (elementTop_3 < windowHeight - elementVisible_3) {
         reveals_6[k].classList.add("show_Block");
       }
       
     }
     for (var j = 0; j < reveals_3.length; j++) {
       var elementTop_2 = reveals_3[j].getBoundingClientRect().top;
       if (elementTop_2 !=-53) {
         reveals_4[j].classList.add("white");
         reveals_4[j].classList.add("colors");
         reveals_5[j].classList.add("colors");
         imglogo.src=require("../images/what.png");
       }
       else if(elementTop_2==-53){
         reveals_5[j].classList.remove("colors");
         reveals_4[j].classList.remove("white");
          reveals_4[j].classList.remove("colors");
     
          imglogo.src=require("../images/woman.png");
    }
   }
   }
   
   window.addEventListener("scroll", reveal);

