<template>
<div class="login">
  <h1>  </h1>
    <form method="post">
        <input type="text" name="u" id="login" placeholder="Username" required="required"/>
        <input type="password" name="p" id="password" placeholder="Password" required="required"/>
      <button type="button" @click="AuthMedicsButton()" class="btn btn-primary btn-block btn-large">  </button>
    </form>
</div>
{{dowload()}}
{{console.log(getAuthMedics)}}
</template>

<script>
import './style.scss';
import { mapGetters, mapActions } from "vuex";
import '../../assets/js/main';

export default {
  name: "PagesLoginDoctor",
  computed: mapGetters(["getAuthMedics"]),
  methods:{...mapActions(["AuthMedics"]),
  dowload:function(){
    if(this.getAuthMedics=="admin"){
     window.location.href = '/AdminPanel';
    }
    else if(this.getAuthMedics=="medics"){
      window.location.href = '/AdminPanel';
    }
  },

    AuthMedicsButton:function(){
    const data = {
    password:document.querySelector("#password").value,
    login:document.querySelector("#login").value,
  };
  this.AuthMedics(data);
  },
  },
 
};
</script>